.featured_item {
  position: relative;
}

.featured_item a {
  position: absolute;
  width: 100%;
  bottom: 0px;
  text-decoration: none;
  right: 0px;
}

.featured_image {
  height: 330px;
  background-size: cover!important;
  background-repeat: no-repeat!important;
}

.featured_caption {
  color: #ffffff;
  top: 0px;
  width: 100%;
  padding: 20px;
  font-weight: 300px;
  font-size: 28px;
  box-sizing: border-box;
}
.newsList_item {
  border: 1px solid #f2f2f2;
  background: #fff;
  padding: 8px 5px 0 5px;

}

.newsList_item h2 {
  font-size: 13px;
  line-height: 21px;
  margin: 5px 0;
  color: #525252;
}

.newsList_item a {
  text-decoration: none;
  flex-grow: 1;
}

.newsList_wrapper {
  box-sizing: border-box;
  opacity: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: all .5s ease-in;
}

.newsList_wrapper_enter {
  opacity: 1;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.flex_wrapper {
  display: flex;
  border-bottom: 2px solid #d5d5d5;
  background: #fff;
}

.left {
  background-size: cover!important;
}

.left div {
  widows: 90px;
  height: 90px;
  background-size: 40px!important;
  background-repeat: no-repeat!important;
  background-position: center center !important;
}

.right {
  flex-grow: 1;
  padding: 10px;
}

.flex_wrapper h2 {
  font-size: 12px;
  color: #525252;
}
.blur_btn {
  text-align: center;
  background: #2196f3;
  border-bottom: 1px solid #d7d7d7;
  font-weight: 500;
  color: #eeeeee;
  padding: 9px;
  cursor: pointer;
  font-size: 15px;

  display: block;
  text-decoration: none;
}
.cardNfo {
  font-size: 11px;
}

.cardNfo i {
  margin-right: 5px;
}

.teamName {
  margin-right: 7px;
  background: #d1d1d1;
  color: #fff;
  padding: 2px 5px; 
}

.date {
  color: #2196f3;
}
.videoList_wrapper h3 {
  text-align: center;
  margin: 20px;
  color: #353535;
  font-weight: 300px;
  font-size: 21px; 
}

.videoListItem_wrapper {
  display: flex;
  border-bottom: 2px solid #d5d5d5;
  background: #ffffff;
}

.videoListItem_wrapper h2 {
  font-size: 13px;
  color: #525252;
}

.left {
  background-size: cover!important;
}

.left div {
  width: 90px;
  height: 90px;
  background-size: 40px!important;
  background-repeat: no-repeat!important;
  background-position: center center!important;
  background: url('/images/play.png');
}

.right {
  flex-grow: 1;
  padding: 10px;
}


.relatedWrapper {
  margin: 0 5px;
}
body {
  margin: 0;
  padding: 0;
  background: #ececec;
  font-family: 'Roboto', sans-serif;
}

a {
  text-decoration: none;
}
.header {
  background: #242424;
  border-bottom: 1px solid #000000;
}

.headerOpt {
  display: flex;

}

.logo {
  flex-grow: 1;
  padding-top: 8px;
}

.logo img {
  width: 35px;
}
.option {
  font-weight: 300;
  font-size: 12px;
  color: #bababa;
  padding: 10px;
  border-top: 1px solid #404040;
}

.option a {
  color: inherit;
  text-decoration: none;
}

.option span {
  color: white;
  margin-right: 10px;
}
.footer {
  margin-top: 20px;
  background: #242424;
  display: flex;
  justify-content: center;
  align-items: center; 
}

.logo img {
  height: 20px;
  padding: 10px;
}

.right {
  flex-grow: 1;
  color: #878787;
  font-size: 12px;
}
.acticleTeamHeader {
  border: 1px solid #c5c5c5;
  border-bottom: 0px;
  background: #fff;
  margin: 5px 5px 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.left {
  width: 57px;
  height: 50px;
  background-size: 65%!important;
  background-repeat: no-repeat!important;
  background-position: center center!important;
}

.right {
  flex-grow: 1;
  padding-left: 10px;
  font-size: 15px;
  font-weight: 300;
}

.acticlePostData {
  border: 1px solid #c5c5c5;
  border-bottom: 0px;
  background: #fff;
  margin: 0px 5px 0 5px;
  padding: 10px;
  font-size: 13px;
  font-weight: 300; 
}

.acticlePostData span {
  font-weight: 500;
}

.articleBody {
  background: #fff;
  margin: 0 5px 0 5px;
  border: 1px solid #c5c5c5;

}

.articleBody h1 {
  font-size: 25px;
  font-weight: 400;
  color: #4d4d4d;
  margin: 15px 0;
}

.articleImage {
  background-size: cover!important;
  width: 100%;
  height: 200px;
  background-position: center center!important;
  background-repeat: no-repeat!important;  
}

.articleText {
  font-weight: 300;
  color: #666666;
  line-height: 23px;
  margin: 15px 0;
}

.videoWrapper {
  background: #fff;
  margin: 0 5px;
  border: 1px solid #c5c5c5;
}

.videoWrapper h1 {
  font-size: 25px;
  font-weight: 400;
  color: #4d4d4d;
  margin: 15px 0;
  padding: 0 10px;
}

.videoWrapper iframe {
  border: 0;
}

.logContainer {
  width: 80%;
  margin: 0 auto;
  height: 100vh;
  padding-top: 50px;
}

.logContainer h2 {
  font-weight: 300;
  text-align: center;
  font-size: 35px;
}

.logContainer input {
  font-size: 17px;
  width: 100%;
  margin-top: 10px;
  padding: 12px 10px;
  box-sizing: border-box;
  font-weight: 300; 
}

.logContainer button {
  background: #03a9f4;
  font-size: 13px;
  padding: 10px;
  color: #fff;
  margin-top: 10px;
}
.labelError {
  color: #ff5722;
  font-weight: 500;
  margin-top: 5px;
  padding: 10px;
}
.postContainer {
  width: 80%;
  margin: 0 auto;
  height: 100vh;
  padding-top: 50px;
}

.postContainer h2 {
  font-weight: 300;
  text-align: center;
  font-size: 35px;
}

.postContainer input {
  font-size: 17px;
  width: 100%;
  margin-top: 10px;
  padding: 12px 10px;
  box-sizing: border-box;
  font-weight: 300; 
}

.postContainer button {
  background: #03a9f4;
  font-size: 13px;
  padding: 10px;
  color: #fff;
  margin-top: 10px;
}
